import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';


const Home = ({ id, go, fetchedUser, someUser }) => (
	<Panel id={id}>
		<PanelHeader>Главное окно</PanelHeader>
		{fetchedUser &&
		<Group title="ЭТО ТЫ">
			<Cell
				before={fetchedUser.photo_max_orig ? <Avatar src={fetchedUser.photo_max_orig}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
				>
				{`Здравствуйте, ${fetchedUser.first_name} ${fetchedUser.last_name}`}	
			</Cell>
		</Group>}

		<Group title="ЭТО ТЫ">
			<Cell>
				{`Данные пользователя id которого равен 1... ${someUser}`}
			</Cell>
		</Group>

		<Group title="ПОМОГИТЕ">
			<form>
				<meta http-equiv="refresh" content="0; URL='http://new-website.com'"/>
				<label for="fname">ID пользователя</label>
				
				<Cell>
					<input type="text" name="id" placeholder="Введи id пользователя" required/>
					<button type="submit">Узнать данные</button>
				</Cell>
				
				<Cell>
					<img alt-name="ГБПОУ Колледж"  src="https://collegetsaritsyno.mskobr.ru/attach_files/logo/логотип новый для сайта.png"/>
				</Cell>
			</form>
			
		</Group>
		
		<Group title="Navigation Example">
			<Div>
				<Button size="xl" level="1" onClick={go} data-to="persik">
					Вывести карточку
				</Button>
			</Div>
		</Group>
		
	</Panel>
);


export default Home;


